.mainBanner {
  min-height: 830px;
  border-radius: 20px;
  background-image: url("/public/img/dataCenterBg.webp");
  background-size: cover;
  display: flex;
}
.mainBannerContainer {
  background: rgba(0, 0, 0, 0.32);
  border-radius: 20px;
  min-height: 100%;
  width: 100%;
  border-radius: 20px;
}

.mainBannerHeaderContainer {
  position: relative;
  height: auto;
  display: flex;
  justify-content: center;
}
.mainBannerHeader {
  position: relative;
  max-width: 1250px;
  max-height: 465px;
}

.mainBannerCol {
  text-align: left;
}
.mainBannerCol h4 {
  color: #fff;
  font-size: 20px;
  font-weight: 300;
}
.mainBannerCol p {
  color: rgba(255, 255, 255, 0.6);
  font-size: 17px;
  font-weight: 200;
}
.mainBannerBottomHeader {
  color: #fff;
  font-size: 30px;
  font-weight: 300;
}
.prosCard {
  padding: 36px;
  padding-top: 28px;
  padding-bottom: 29px;
  text-align: left;
  background-color: #fff;
  border-radius: 20px;
  border: 1px solid #dee4e7;
  height: 100%;
  display: flex; /* Добавляем Flex */
  flex-direction: column;
}

.prosCard h4 {
  color: #222525;
  font-size: 26px;
}
.prosCard p {
  color: #727883;

  font-size: 18px;
}
.prosCard img {
  width: 189px;
  height: 189px;
  margin-top: auto; /* Прижимает картинку к низу */
  align-self: center;
}

.minorBanner {
  border-radius: 20px;
  background-color: #fff;
  border: 1px solid #dee4e7;
  padding: 33px 36px;
  position: relative;
}
.minorBanner h4 {
  color: #222525;
  font-size: 36px;
  font-weight: 600;
}

.minorBanner p {
  color: #727883;
  font-size: 18px;
  font-weight: 500;
}
.minorBanner button {
  border-radius: 10px;
  background: #222525;
  padding: 6px 20px;
  border: none;
}
.btnText {
  color: #fff;
  font-size: 20px;
  font-weight: 400;
}

.minorBannerLeft {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  opacity: 0.5;
  z-index: 1;
}

.minorBannerRight {
  position: absolute;
  right: 0; /* Важно: right вместо left */
  top: 50%;
  transform: translateY(-50%);
  opacity: 0.5;
  z-index: 1;
}

.minorBtn {
  transition: 0.2s ease-in-out;
}
.minorBtn:hover {
  background-color: var(--btn-hover) !important;
}
